// utils.js

import http from "http";

const serverHost = "192.168.1.101";
const serverPort = 3000;
const POST = "post";
const GET = "get";

// @param{Object} data 可以被 JSON 化的 {user, method, action}
// @return{void}
function postActions(data) {
	let config = {
		host: serverHost,
		port: serverPort,
		method: POST,
		headers: {
			data: JSON.stringify(data),
		},
	};
	http.request(config, function () {}).end();
}

// @param{Object} data 可以被 JSON 化的 {user, method, action}
// @return{Promise}
function getActions(data) {
	let config = {
		host: serverHost,
		port: serverPort,
		method: GET,
		headers: {
			data: JSON.stringify(data),
		},
	};

	return new Promise((resolve) => {
		http
			.request(config, function (res) {
				res.on("data", (data) => {
					resolve(JSON.parse(data));
				});
			})
			.end();
	});
}

export default {
	postActions,
	getActions,
};
