<template>
	<div id="app">
		<div @click="handleControlAction">
			<button>up</button>
			<button>right</button>
			<button>down</button>
			<button>left</button>
			<span>last action: {{ lastActions["0"] }}</span>
		</div>
		<div @click="handleControlAction">
			<button>up</button>
			<button>right</button>
			<button>down</button>
			<button>left</button>
			<span>last action: {{ lastActions["1"] }}</span>
		</div>
		<div>
			<div>user name:</div>
			<input v-model="user" />
			<div>{{ user }}</div>
		</div>
		<div>
			<div v-for="(action, index) in historyActions" :key="index">
				{{ action }}
			</div>
		</div>
	</div>
</template>

<script>
import util from "./utils/util.js";

export default {
	name: "App",
	components: {},
	data() {
		return {
			lastActions: {},
			historyActions: ["无", "无"],
			user: 0,
		};
	},
	mounted() {
		setInterval(() => {
			util
				.getActions({
					user: this.user,
					method: "get",
				})
				.then((res) => {
					let { actions } = res;
					for (let action of actions || []) {
						console.log(action);
						let { user, action: item } = action;
						this.lastActions[user] = item;
						this.historyActions.push(item);
					}
				})
				.catch((err) => {
					console.log(err);
				});
		}, 50);
	},
	methods: {
		handleControlAction(e) {
			let action = e.target.innerText;
			// let user = e.target.parentNode.dataset.idx;
			this.lastActions[this.user] = action;
			this.historyActions.push(action);

			util.postActions({
				user: this.user,
				action,
				method: "post",
			});
		},
	},
};
</script>

